            <el-checkbox v-model="includeLowercase" @change="generatePassword">å°å†™å­—æ¯ (a-z)</el-checkbox>
            <el-checkbox v-model="includeNumbers" @change="generatePassword">æ•°å­— (0-9)</el-checkbox>
