<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'PasswordGeneratorView',
  data() {
    return {
      length: 16,
      includeUppercase: true,
      includeLowercase: true,
      includeNumbers: true,
      includeSymbols: true,
      excludeChars: '',
      count: 5,
      passwords: []
    }
  },
  mounted() {
    this.generatePasswords()
  },
  methods: {
    generatePassword() {
      let charset = ''
      
      if (this.includeUppercase) charset += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
      if (this.includeLowercase) charset += 'abcdefghijklmnopqrstuvwxyz'
      if (this.includeNumbers) charset += '0123456789'
      if (this.includeSymbols) charset += '!@#$%^&*()_+-=[]{}|;:,.<>?'
      
      // æ’é™¤æŒ‡å®šå­—ç¬¦
      if (this.excludeChars) {
        for (const char of this.excludeChars) {
          charset = charset.replace(new RegExp(char.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'), 'g'), '')
        }
      }
      
      if (!charset) return ''
      
      let password = ''
      for (let i = 0; i < this.length; i++) {
        password += charset.charAt(Math.floor(Math.random() * charset.length))
      }
      
      return password
    },
    
    generatePasswords() {
      this.passwords = []
      for (let i = 0; i < this.count; i++) {
        const password = this.generatePassword()
        if (password) {
          this.passwords.push({
            value: password,
            visible: false,
            strength: this.calculateStrength(password)
          })
        }
      }
    },
    
    calculateStrength(password) {
      let score = 0
      
      // é•¿åº¦æ£€æŸ¥
      if (password.length >= 8) score += 25
      if (password.length >= 12) score += 25
      
      // å­—ç¬¦ç±»å‹æ£€æŸ¥
      if (/[a-z]/.test(password)) score += 10
      if (/[A-Z]/.test(password)) score += 10
      if (/[0-9]/.test(password)) score += 10
      if (/[^A-Za-z0-9]/.test(password)) score += 20
      
      // å¤æ‚åº¦æ£€æŸ¥
      const uniqueChars = new Set(password).size
      if (uniqueChars / password.length > 0.7) score += 10
      
      let level = 'weak'
      let text = 'å¼±'
      
      if (score >= 80) {
        level = 'strong'
        text = 'å¼º'
      } else if (score >= 60) {
        level = 'medium'
        text = 'ä¸­ç­‰'
      } else if (score >= 40) {
        level = 'fair'
        text = 'ä¸€èˆ¬'
      }
      
      return { score: Math.min(score, 100), level, text }
    },
    
    toggleVisibility(index) {
      this.passwords[index].visible = !this.passwords[index].visible
    },
    
    async copyPassword(password) {
      try {
        const success = await ClipboardUtil.copyText(password)
        if (success) {
          this.$message.success('å¯†ç å·²å¤åˆ¶åˆ°å‰ªè´´æ¿ï¼')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥ï¼')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },
    
    async copyAllPasswords() {
      const allPasswords = this.passwords.map(p => p.value).join('\n')
      try {
        const success = await ClipboardUtil.copyText(allPasswords)
        if (success) {
          this.$message.success('æ‰€æœ‰å¯†ç å·²å¤åˆ¶åˆ°å‰ªè´´æ¿ï¼')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥ï¼')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    }
  }
}
</script>

